import SongList from '@/components/songList/SongList.vue'
import {  getPersonalized } from '@/api/index.js'
export default {
	data() {
		return {
		navList: [
			{ name: '私人FM', imd: 1 },
			{ name: '最嗨电音', imd: 2 },
			{ name: 'ACG专区', imd: 3 },
			{ name: 'Sati空间', imd: 4 },
			{ name: '私藏推荐', imd: 5 },
			{ name: '因乐交友', imd: 6 },
			{ name: '亲子频道', imd: 7 },
			{ name: '古典专区', imd: 8 }
		],
		recommendSongs: [],
		};
	},
	components: {
		SongList
	},
	onLoad() {
		this.getPersonalized()
	},
	methods: {
		getPersonalized() {
			getPersonalized({ limit: 6 }).then(res=>{
				this.recommendSongs = res.result
			})
		}
	},
}
